import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    getReporteHostigamientoRango(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-social/prevencion-hostigamiento/denuncias/grafico/rango', { params: data })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

    getReporteTasaCrecimiento() {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-social/prevencion-hostigamiento/denuncias/grafico/indicadores')
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

  },
}
